body{
    padding: 0;
    margin:0 ;
   
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    background-color: rgba(215, 230, 200, 0.5);
    

}

#container{
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-columns: 2fr 10fr;
    grid-template-rows:1fr 7fr ;
}
#box1{
    background-color: rgba(224, 238, 224, 0.5);
    grid-column:1/3;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 10px;
    

    
}
#logo{
    background-image: url("fork.png");
    background-repeat: no-repeat;
    height: 40px;
    width: 40px;
}

#shoppingCart{
    background-image: url("shopping-cart.png");
    background-repeat: no-repeat;
    background-size: 35px 35px;
    background-position: 7px 8px;
    height: 50px;
    width: 50px;
    background-color: rgb(65, 71, 88);
    border-radius: 50%;
    margin-top: 5px;
    margin-right: 5px;
}
/*aviso de carrito lleno*/

#cartFull{
    height: 20px;
    width: 20px;
   
    border-radius: 50%;
    background-color: rgb(37, 187, 70);
    position: relative;
    top: 80%;
    left: 60%;
    font-weight: bold; 
    text-align: center;
    line-height:20px;
    color: rgb(235, 224, 209);
    
}



#box2{
    background-color: rgba(215, 230, 200, 0.5);
    display:flex;
    flex-direction:column;
    justify-content: space-around ;
    padding: 10px;
   
}
#box3{
    
    grid-column: 2/3;
    grid-row: 2/3;
    

}

#homeLegend{
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
/*product container and products list items*/

.productContainer{
    height: 50%;
    width: 50%;
    padding: 10px;
    

}
.product{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
   

}

